
.draggable-button-wrapper {
  position: relative;
  width: 14px;
  height: 14px;
}
.draggable-button-wrapper .draggable-button {
  position: relative;
  top: -4px;
  left: -2px;
  width: 14px;
  height: 14px;
  border: 2px solid #5cadff;
  border-radius: 50%;
  background-color: #fff;
  -webkit-transition: all .2s linear;
  transition: all .2s linear;
  opacity: 0;
}
.draggable-button-wrapper .ant-tooltip {
  top: -50px !important;
  left: 50% !important;
  margin-left: -19px;
}



